:root {
  --text-primary: #111827;
  --text-secondary: #4b5563;
  --bg-main: #f9fafb;
  --border-color: #e5e7eb;
  --accent-color-static: #4f46e5;
  --success-color: #16a34a;
  --remove-color: #ef4444;
  --transition-speed: .3s;
}

body {
  background-color: var(--bg-main);
  min-height: 100vh;
  color: var(--text-primary);
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 1rem;
  font-family: system-ui, -apple-system, sans-serif;
  display: flex;
}

.permission-card {
  border: 1px solid var(--border-color);
  text-align: center;
  background: #fff;
  border-radius: 16px;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
  max-width: 450px;
  padding: 2.5rem;
  animation: .5s ease-out fadeIn;
  display: flex;
  box-shadow: 0 4px 12px #0000000d, 0 10px 30px #00000012;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.icon-container {
  background-color: #f0f2ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  margin-bottom: .5rem;
  display: flex;
}

.icon-container svg {
  width: 36px;
  height: 36px;
  color: var(--accent-color-static);
  transition: color var(--transition-speed) ease;
}

h1 {
  color: var(--text-primary);
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
}

p {
  color: var(--text-secondary);
  max-width: 380px;
  margin: 0;
  font-size: .95rem;
  line-height: 1.6;
}

.spinner {
  border: 3px solid #4f46e533;
  border-top-color: var(--accent-color-static);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: .5rem 0;
  animation: 1s linear infinite spin;
}

body:not(.loading) .spinner, body.loading .icon-container {
  display: none;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

body.denied-state .icon-container {
  background-color: #feeeee;
}

body.denied-state .icon-container svg {
  color: var(--remove-color);
}

body.granted-state .icon-container {
  background-color: #f0fdf4;
}

body.granted-state .icon-container svg {
  color: var(--success-color);
}
